.comment {
  position: fixed;
  left: 0;
  bottom: -2000px;
  right: 0;
  background-color: #fff;
  z-index: 1004;
  transform: translate(0, 100%);
}
.comment.show {
    position: fixed;
    top: 0;
    bottom: 0;
    transform: translate(0, 0);
}
.comment .md-toolbar {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
}
.comment .comment-wrap {
    height: 100%;
    padding-top: 0.96rem;
    overflow-y: auto;
}
.comment .music-info {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 1.62rem;
    padding: 0 0.16rem;
}
.comment .music-info .music-pic {
      width: 1.3rem;
      height: 1.3rem;
}
.comment .music-info .music-name {
      -ms-flex: 1;
          flex: 1;
      margin-left: 0.2rem;
}
.comment .music-info .music-name p:nth-child(2) {
        margin-top: 0.12rem;
        font-size: 0.24rem;
        color: #626262;
}
.comment .comment-title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: 0.48rem;
    padding-left: 0.2rem;
    color: #626262;
    font-size: 0.24rem;
    background-color: #f0f0f0;
}
.comment .comment-content li {
    display: -ms-flexbox;
    display: flex;
    padding-top: 0.2rem;
}
.comment .comment-content .comment-avatar {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
