}
.comment .comment-content .comment-body .comment-body-title p:nth-of-type(2) {
        margin-top: 0.06rem;
        color: #616261;
        font-size: 0.24rem;
}
.comment .comment-content .comment-body .comment-body-content {
      padding: 0.2rem;
      padding-left: 0;
      border-bottom: 1px solid #e0e0e0;
}
.comment .comment-content .comment-body .comment-body-content p {
        font-size: 0.28rem;
        line-height: 1.5;
        color: #333;
}
.comment .comment-loading {
    position: absolute;
    left: 50%;
    top: 50%;
