
@font-face {
  font-family: 'iconfont';
  /* project id 311056 */
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot");
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.woff") format("woff"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.ttf") format("truetype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.svg#iconfont") format("svg");
}
.player {
  position: fixed;
  left: 0;
  bottom: -2000px;
  right: 0;
  background: url("../assets/images/bg.jpg");
  background-color: #fff;
  z-index: 1001;
}
.player.show {
    top: 0;
    bottom: 0;
}
.player.animated {
    animation-duration: .1s;
}
.player .player-wrap {
    z-index: 1002;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}
.player-header {
  height: 1.12rem;
  background-color: transparent !important;
}
.player-header h2 {
    line-height: 1.1;
    font-size: 0.32rem !important;
}
.player-header h2 span {
      color: #ccc;
      font-size: 0.24rem;
}
.player-panel1 {
  position: absolute;
  left: 0;
  right: 0;
  top: 1.12rem;
  bottom: 2.2rem;
  /*display: none;*/
}
.player-panel1 .player-line {
    height: 1px;
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(255, 255, 255, 0.3), rgba(0, 0, 0, 0));
}
.player-panel1 .player-needle {
    position: absolute;
    z-index: 1;
    top: -1px;
    left: 46%;
    height: 2.8rem;
}
.player-panel1 .player-needle img {
      height: 100%;
}
.player-panel1 .record-cover {
    width: 5rem;
    height: 5rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    position: absolute;
    left: 50%;
    top: 1.6rem;
    border-radius: 50%;
    transform: translate(-50%, 0);
    background-color: rgba(255, 255, 255, 0.2);
}
.player-panel1 .record-cover .record-bg {
      width: 4.92rem;
      height: 4.92rem;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      background-image: url("../assets/images/record-bg.png");
      background-size: 100% 100%;
}
.player-panel1 .record-cover .record-bg.rotate-bg {
        animation: rotatebg 20s linear infinite;
}
@keyframes rotatebg {
0% {
    transform: rotate(0deg);
}
50% {
    transform: rotate(180deg);
}
100% {
    transform: rotate(360deg);
}
}
.player-panel1 .record-cover .record-bg .music-bg {
        width: 3.36rem;
        height: 3.36rem;
        border-radius: 50%;
        overflow: hidden;
}
.player-panel1 .record-cover .record-bg .music-bg img {
          width: 100%;
          height: 100%;
}
.player-panel1 .player-bar {
    position: absolute;
    left: 50%;
    bottom: 0.2rem;
    transform: translate(-50%, 0);
    display: -ms-flexbox;
    display: flex;
    width: 70%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    color: #fff;
}
.player-panel1 .player-bar .bar-item:nth-child(3) {
      position: relative;
}
.player-panel1 .player-bar .bar-item:nth-child(3) .comment-total {
        position: absolute;
        top: -0.1rem;
        right: -0.24rem;
        font-size: 0.16rem;
        width: 0.43rem;
        height: 0.2rem;
        display: -ms-flexbox;
        display: flex;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
        background-color: rgba(0, 0, 0, 0.1);
}
.player-panel1 .player-bar .iconfont {
      font-size: 0.36rem;
}
.player-panel1 .player-bar .iconfont:nth-child(2) {
        font-size: 0.4rem;
}
.player-panel2 {
  position: absolute;
  left: 0;
  right: 0;
  top: 1.12rem;
  bottom: 2.2rem;
  color: #fff;
  overflow: hidden;
}
.player-panel2 ul {
    padding-top: 65%;
    transition: all .8s;
}
.player-panel2 ul li {
      font-size: 0.28rem;
      /*padding: 5px 0;*/
      line-height: 30px;
      text-align: center;
      color: #ccc;
}
.player-panel2 ul li.on {
        color: #fff;
}
.player-ctrl {
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 2.2rem;
  color: #fff;
  background-color: transparent;
}
.player-ctrl .progress {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 0.38rem;
    margin-top: 0.3rem;
}
.player-ctrl .progress span {
      font-size: 0.16rem;
}
.player-ctrl .progress .progress-bar {
      -ms-flex: 1;
          flex: 1;
      margin: 0 0.1rem;
      height: 0.04rem;
      background-color: #9f9f9f;
}
.player-ctrl .progress .progress-bar .cur-progress {
        width: 50%;
        height: 100%;
        background-color: #d33a31;
}
.player-ctrl .progress .progress-bar .idot {
        position: relative;
        width: 13px;
        height: 13px;
        border-radius: 50%;
        background-color: #fff;
        top: -0.16rem;
}
.player-ctrl .player-conctrl {
    margin-top: 0.44rem;
    padding: 0 0.3rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.player-ctrl .player-conctrl > div i {
      font-size: 0.4rem;
}
.player-ctrl .player-conctrl .ctrl {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      width: 0.8rem;
      height: 0.8rem;
      border: 1px solid #fff;
      border-radius: 50%;
}
.player-ctrl .player-conctrl .ctrl i {
        margin-top: 0.06rem;
        margin-left: 0.06rem;
        font-size: 0.36rem;
}
.player-ctrl .player-conctrl .ctrl i.icon-zanting {
          margin-top: 0;
          margin-left: 0;
}
.player-bg {
  position: absolute;
  left: -30%;
  top: -30%;
  width: 180%;
  height: 180%;
  z-index: 1001;
  background-image: url("http://p1.music.126.net/y1JN20Yevr4e8j75QeWtQA==/1771313232347170.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  filter: blur(70px) brightness(70%);
}



