.play-list {
  position: fixed;
  left: 0;
  bottom: 0;
  right: 0;
  height: 7.88rem;
  background-color: #fff;
  z-index: 1003;
  transform: translate(0, 100%);
}
.play-list.showlist {
    transition: all .3s;
    transform: translate(0, 0);
}
.play-list .play-list-title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 0.2rem;
    height: 0.8rem;
    font-size: 0.28rem;
    border-bottom: 1px solid #e0e0e0;
}
.play-list .play-list-content {
    height: 7.08rem;
    overflow-y: auto;
}
.play-list .play-list-content .play-list-item {
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: justify;
          justify-content: space-between;
      -ms-flex-align: center;
          align-items: center;
      height: 0.84rem;
      padding: 0 0.2rem;
      border-bottom: 1px solid #e0e0e0;
}
.play-list .play-list-content .play-list-item .song-name {
        -ms-flex: 1;
            flex: 1;
        font-size: 0.28rem;
        line-height: 1.5;
        margin-right: 0.2rem;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.play-list .play-list-content .play-list-item .song-name .iconfont {
          display: none;
}
.play-list .play-list-content .play-list-item .song-name.current {
          color: #d33a31;
}
.play-list .play-list-content .play-list-item .song-name.current .iconfont {
            display: inline;
            margin-top: 0.04rem;
            margin-right: 0.1rem;
            font-size: 0.28rem;
}
.play-list .play-list-content .play-list-item .song-name.current span {
            color: #d33a31;
}
.play-list .play-list-content .play-list-item .song-name span {
          color: #909090;
          font-size: 0.2rem;
}
.play-list .play-list-content .play-list-item .song-del .iconfont {
        font-size: 0.36rem;
}



