
@charset "UTF-8";
@font-face {
  font-family: 'iconfont';
  /* project id 311056 */
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot");
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.woff") format("woff"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.ttf") format("truetype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.svg#iconfont") format("svg");
}
.index-tab {
  display: -ms-flexbox;
  display: flex;
}
.index-tab li {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 0 !important;
    height: 0.7rem;
    -ms-flex: 1;
        flex: 1;
}
.index-tab li a:not(.md-button) {
      width: 100%;
      height: 100%;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: center;
          justify-content: center;
      -ms-flex-align: center;
          align-items: center;
      font-size: 0.28rem;
      color: #666;
      border-bottom: 2px solid #fff;
}
.index-tab li a:not(.md-button):hover {
        color: #d33a31;
        text-decoration: none;
        border-bottom-color: #d33a31;
}
.md-button-toggle.index {
  position: fixed;
  top: 0.96rem;
  left: 0;
  right: 0;
  z-index: 1000;
}
.md-theme-default.md-button-toggle {
  display: -ms-flexbox;
  display: flex;
}
.md-theme-default.md-button-toggle .md-button {
    -ms-flex: 1;
        flex: 1;
    border-bottom: 3px solid #fff;
    background-color: #fff;
}
.md-theme-default.md-button-toggle .md-toggle {
    color: #d33a31;
    background-color: #fff !important;
    border-bottom: 3px solid #d33a31;
}

/* 上面是为了保证滑动的时候不出现抖动情况 */
.index-child-view {
  position: absolute;
  left: 0;
  top: 1.68rem;
  /*height: 100%;*/
  width: 100%;
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  background-color: #fff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}

/* 当child-view的内容过多时会撑开child-view使得内部能够滚动 */
.slide-left-enter, .slide-right-leave-active {
  opacity: 0;
  transform: translate(7.5rem, 0);
  transition-delay: .3s;
  -webkit-transition-delay: .3s;
}
.slide-left-leave-active, .slide-right-enter {
  opacity: 0;
  transform: translate(-7.5rem, 0);
  transition-delay: .3s;
  -webkit-transition-delay: .3s;
}
.slide-enter-active {
  transition: all .3s ease;
}
.slide-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}



