
@font-face {
  font-family: 'iconfont';
  /* project id 311056 */
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot");
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.woff") format("woff"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.ttf") format("truetype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.svg#iconfont") format("svg");
}
#search > ul {
  min-height: 8rem;
}
#search .md-toolbar {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 1000;
}
#search .md-toolbar input {
    -ms-flex: 1;
        flex: 1;
    margin-right: 0.1rem;
    height: 0.6rem;
    background-color: transparent;
    border: none;
    padding-bottom: 0;
    border-bottom: 1px solid #fff;
    color: #fff;
}
#search .md-toolbar input::-webkit-input-placeholder {
      color: #e07770;
}
#search .md-toolbar input:focus {
      outline: none;
}
#search .md-toolbar .iconfont {
    display: inline-block;
    font-size: 0.4rem;
}
#search .hot-search {
  box-sizing: border-box;
  padding: 2rem 0.2rem;
}
#search .hot-search p {
    font-size: 0.24rem;
    color: #666;
    margin-bottom: 0.22rem;
}
#search .hot-search .hot-search-list {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
#search .hot-search .hot-search-list li {
      padding: 0.14rem 0.22rem;
      margin: 0rem 0.16rem 0.16rem 0;
      border: 1px solid #dcdcdc;
      border-radius: 0.5rem;
      font-size: 0.28rem;
}
#search .md-button-toggle.search-button-toggle {
  position: fixed;
  top: 0.96rem;
  left: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  z-index: 1000;
}
#search .md-button-toggle.search-button-toggle .md-button {
    -ms-flex: 1;
        flex: 1;
    font-size: 0.26rem;
    border-bottom: 3px solid #fff;
    background-color: #fff;
}
#search .md-button-toggle.search-button-toggle .md-toggle {
    color: #d33a31;
    background-color: #fff !important;
    border-bottom: 3px solid #d33a31;
}
#search .search-suggest {
  position: fixed;
  z-index: 1001;
  top: 0.96rem;
  left: 0.3rem;
  right: 0.66rem;
  background-color: #f3f3f3;
  box-shadow: 3px 3px 10px #ccc, -1px 3px 10px #ccc;
  transition: all .3s;
}
#search .search-suggest .search-key {
    padding: 0.3rem 0.26rem;
    color: #679dc5;
}
#search .search-suggest ul li {
    padding: 0.3rem 0.26rem;
    border-top: 1px solid #e5e5e5;
    color: #666;
    font-size: 0.28rem;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
}
#search .search-suggest ul li .iconfont {
      margin-right: 0.16rem;
}
#search .search-child-view {
  position: absolute;
  top: 1.728rem;
  left: 0;
  width: 100%;
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  background-color: #f2f4f5;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
#search .search-slide-left-enter, #search .search-slide-right-leave-active {
  opacity: 0;
  transform: translate(7.5rem, 0);
  transition-delay: .3s;
  -webkit-transition-delay: .3s;
}
#search .search-slide-left-leave-active, #search .search-slide-right-enter {
  opacity: 0;
  transform: translate(-7.5rem, 0);
  transition-delay: .3s;
  -webkit-transition-delay: .3s;
}
#search .search-slide-enter-active {
  transition: all .3s ease;
}
#search .search-slide-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}



