
@font-face {
  font-family: 'iconfont';
  /* project id 311056 */
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot");
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.woff") format("woff"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.ttf") format("truetype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.svg#iconfont") format("svg");
}
.md-toolbar.root {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
}
.md-toolbar.root a {
    color: rgba(255, 255, 255, 0.87) !important;
}
.md-toolbar.root .md-title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
}
.md-toolbar.root .md-title .iconfont {
      color: #e58983;
}
.md-toolbar.root .md-title .iconfont.active {
        color: #fff;
}
.md-toolbar.root .md-title .iconfont:not(:last-of-type) {
        margin-right: 0.3rem;
}
.md-toolbar.root .md-title .iconfont:nth-of-type(1) {
        font-size: 0.4rem;
}
.md-toolbar.root .md-title .iconfont:nth-of-type(2) {
        font-size: 0.52rem;
}
.md-toolbar.root .md-title .iconfont:nth-of-type(3) {
        font-size: 0.48rem;
}
.root-child-view {
  transition: all 0.3s cubic-bezier(0.55, 0, 0.1, 1);
  overflow: auto;
}
.root-slide-left-enter,
.root-slide-right-leave-active {
  opacity: 0;
  transform: translate(7.5rem, 0);
  transition-delay: 0.3s;
  -webkit-transition-delay: 0.3s;
}
.root-slide-left-leave-active,
.root-slide-right-enter {
  opacity: 0;
  transform: translate(-7.5rem, 0);
  transition-delay: 0.3s;
  -webkit-transition-delay: 0.3s;
}
.root-slide-enter-active {
  transition: all 0.3s ease;
}
.root-slide-leave-active {
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}



